import React, { Component } from 'react';
import LazyLoad from 'react-lazy-load';
import './Project.css';
import ReactHtmlParser from 'react-html-parser';
import API from '../Personal-Site-Data.json';
import '../Homepage/bootstrap.min.css';

// const smallImg = 'w320';
// const mediumImg = 'w768';
// const largeImg = 'w1280';

class Project extends Component {
  constructor(){
    super();
    this.state = {
     highlights:[
       {
         headerimage: "/images/placeholder/generic-header.png",
         imageone: "/images/placeholder/generic-image.png",
         title: "Loading...",
         description: " "
       }
     ],
     loaded: 'loading'
    };
  }
  componentDidMount() {
    let articleNumberString = this.props.match.params.id;
    this.setState({ highlights:  [API.Highlight[articleNumberString]] });
    this.setState({ loaded: 'loaded' });
    window.scrollTo(0, 0);
  }
  render() {
    const highlightProject = this.state.highlights.map((highlight) =>
          <div key={highlight.id} className="container-fluid p-0">
            <div className="col-md-12 px-0 articleFeatureImage">
              <img alt={highlight.alt} className={`w-100 skeleton projectHeader ${this.state.loaded}`} src={`${highlight.headerimage}`} />
            </div>
            <div className="moveUp">
                  <div className="container px-4">
                    <div className="col-md-12">
                      <h1 className="featured text-center pt-5">{highlight.title}</h1>
                      <p className="grey project ml-auto mr-auto text-center py-2">{highlight.description}</p>
                      <hr className="pb-5 mb-2"/>
                    </div>
                      <div className={`row moveUp ${highlight.displayone}`}>
                      <div className="col-md-6">
                        <h2 className="dark">{highlight.subheadingone}</h2>
                        <div className="grey project pt-4">{ ReactHtmlParser(highlight.copyone) }</div>
                      </div>
                      <div className="col-md-6">
                        <div className="mt-3 fillHeight">
                          <div className="featureImage">
                            <LazyLoad debounce={false} offsetVertical={200}>
                                <img className={`w-100  ${this.state.loaded}`}sizes="(max-width: 320px) 280px, (max-width: 768px) 728px, 1024px"  alt={highlight.imageonecaption} src={`${highlight.imageone}`} data-holder-rendered="true" />
                            </LazyLoad>
                            <div className="highlight caption float-right">{highlight.imageonecaption}</div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className={`container px-4 ${highlight.displaytwo}`}>
                    <div className="col-md-12">
                      <hr className="pb-5 mt-5 mb-2"/>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <h2 className="dark">{highlight.subheadingtwo}
                        </h2>
                        <div className="grey project pt-4">{ ReactHtmlParser(highlight.copytwo) }
                        </div>
                      </div>
                      <div className="col-md-6">
                        <div className="mt-3 fillHeight">
                          <div className="featureImage">
                            <LazyLoad debounce={false} offsetVertical={200}>
                              <img className="w-100" alt={highlight.imagetwocaption} src={`${highlight.imagetwo}`} data-holder-rendered="true" />
                            </LazyLoad>
                            <div className="highlight caption float-right">
                              {highlight.imagetwocaption}
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className={`container px-4 ${highlight.displaythree}`}>
                    <div className="col-md-12">
                      <hr className="pb-5 mt-5 mb-2"/>
                    </div>
                    <div className="row">
                      <div className="col-md-6">
                        <h2 className="dark">{highlight.subheadingthree}
                        </h2>
                        <div className="grey project pt-4">
                          { ReactHtmlParser(highlight.copythree) }
                        </div>
                      </div>
                      <div className="col-md-6">
                        <div className="mt-3 fillHeight">
                          <div className="featureImage">
                            <LazyLoad debounce={false} offsetVertical={200}>
                              <img className="w-100" data-src="holder.js/100px225?theme=thumb&amp;bg=55595c&amp;fg=eceeef&amp;text=Thumbnail" alt={highlight.imagethreecaption}  src={highlight.imagethree}  data-holder-rendered="true" />
                            </LazyLoad>
                            <div className="highlight caption float-right">
                              {highlight.imagethreecaption}
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className={`container px-4 ${highlight.displayfour}`}>
                    <div className="col-md-12">
                      <hr className="pb-5 mt-5 mb-2"/>
                    </div>
                      <div className="row">
                      <div className="col-md-6">
                        <h2 className="dark">{highlight.subheadingfour}</h2>
                        <div className="grey project pt-4">{ ReactHtmlParser(highlight.copyfour) }</div>
                      </div>
                      <div className="col-md-6">
                        <div className="mt-3 fillHeight">
                          <div className="featureImage">
                            <LazyLoad debounce={false} offsetVertical={200}>
                              <img className="w-100" data-src="holder.js/100px225?theme=thumb&amp;bg=55595c&amp;fg=eceeef&amp;text=Thumbnail" alt={highlight.imagefourcaption}  src={highlight.imagefour}  data-holder-rendered="true" />
                            </LazyLoad>
                            <div className="highlight caption float-right">{highlight.imagefourcaption}</div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className={`container px-4 ${highlight.displayfive}`}>
                    <div className="col-md-12">
                      <hr className="pb-5 mt-5 mb-2"/>
                    </div>
                      <div className="row">
                      <div className="col-md-6">
                        <h2 className="dark">{highlight.subheadingfive}</h2>
                        <div className="grey project pt-4">{ ReactHtmlParser(highlight.copyfive) }</div>
                      </div>
                      <div className="col-md-6">
                        <div className="mt-3 fillHeight">
                          <div className="featureImage">
                            <LazyLoad debounce={false} offsetVertical={200}>
                              <img className="w-100" data-src="holder.js/100px225?theme=thumb&amp;bg=55595c&amp;fg=eceeef&amp;text=Thumbnail" alt={highlight.imagefivecaption}  src={highlight.imagefive}  data-holder-rendered="true" />
                            </LazyLoad>
                            <div className="highlight caption float-right">{highlight.imagefivecaption}</div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className={`container px-4 ${highlight.displaysix}`}>
                    <div className="col-md-12">
                      <hr className="pb-5 mt-5 mb-2"/>
                    </div>
                      <div className="row">
                      <div className="col-md-6">
                        <h2 className="dark">{highlight.subheadingsix}</h2>
                        <div className="grey project pt-4">{ ReactHtmlParser(highlight.copysix) }</div>
                      </div>
                      <div className="col-md-6">
                        <div className="mt-3 fillHeight">
                          <div className="featureImage">
                            <LazyLoad debounce={false} offsetVertical={200}>
                              <img className="w-100" data-src="holder.js/100px225?theme=thumb&amp;bg=55595c&amp;fg=eceeef&amp;text=Thumbnail" alt={highlight.imagesixcaption}  src={highlight.imagesix}  data-holder-rendered="true" />
                            </LazyLoad>
                            <div className="highlight caption float-right">{highlight.imagesixcaption}</div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className={`container px-4 ${highlight.displayseven}`}>
                    <div className="col-md-12">
                      <hr className="pb-5 mt-5 mb-2"/>
                    </div>
                      <div className="row">
                      <div className="col-md-6">
                        <h2 className="dark">{highlight.subheadingseven}</h2>
                        <div className="grey project pt-4">{ ReactHtmlParser(highlight.copyseven) }</div>
                      </div>
                      <div className="col-md-6">
                        <div className="mt-3 fillHeight">
                          <div className="featureImage">
                            <LazyLoad debounce={false} offsetVertical={200}>
                              <img className="w-100" data-src="holder.js/100px225?theme=thumb&amp;bg=55595c&amp;fg=eceeef&amp;text=Thumbnail" alt={highlight.imagesevencaption}  src={highlight.imageseven}  data-holder-rendered="true" />
                            </LazyLoad>
                            <div className="highlight caption float-right">{highlight.imagesevencaption}</div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className={`container px-4 ${highlight.displayeight}`}>
                    <div className="col-md-12">
                      <hr className="pb-5 mt-5 mb-2"/>
                    </div>
                      <div className="row">
                      <div className="col-md-6">
                        <h2 className="dark">{highlight.subheadingeight}</h2>
                        <div className="grey project pt-4">{ ReactHtmlParser(highlight.copyeight) }</div>
                      </div>
                      <div className="col-md-6">
                        <div className="mt-3 fillHeight">
                          <div className="featureImage">
                            <LazyLoad debounce={false} offsetVertical={200}>
                              <img className="w-100" data-src="holder.js/100px225?theme=thumb&amp;bg=55595c&amp;fg=eceeef&amp;text=Thumbnail" alt={highlight.imageeightcaption}  src={highlight.imageeight}  data-holder-rendered="true" />
                            </LazyLoad>
                            <div className="highlight caption float-right">{highlight.imageeightcaption}</div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
              </div>
          </div>
    );
    return (
      <div className="projectPage">
          {highlightProject}
        <br />
      </div>
    );
  }
}

export default Project;



